import React, {Component} from 'react';

class GoogleMap extends Component {
  constructor(props){
    super(props);
  }

  componentDidMount(){
    new google.maps.Map(this.refs.map, {
      zoom: 12,
      center: {
        lat: this.props.lat,
        lng: this.props.lon
      }
    });
  }

  render(){
    return <div style={{ height: '150px', width: '100%' }} ref="map"/>
  }
}

export default GoogleMap;